/* Always set the map height explicitly to define the size of the div
       * element that contains the map. */

#root {
  height: 100%;
  width: 100%;
  position: relative;
  --softorange: #F4A259;
  --tomatored: #F25C66;
  --mediumblu: #1E272D;
}

.App {
  width: 100%;
  height: 100%;
  color: white;
}

/* Optional: Makes the sample page fill the window. */

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

.left-container::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */

.left-container {
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}

.left-container {  
  box-shadow: rgba(240, 46, 170, 0.4) 0px 5px, rgba(240, 46, 170, 0.3) 0px 10px, rgba(240, 46, 170, 0.2) 0px 15px, rgba(240, 46, 170, 0.1) 0px 20px, rgba(240, 46, 170, 0.05) 0px 25px;
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  top: 50px;
  right: 50px;
  max-height: 80vh;
  max-width: 25vw;
  z-index: 9999;
  overflow: scroll;
  font-size: 24px;
  border: none;
  border-radius: 8px;
  resize: both;
  background: #ffd73e33;
  border-image: url("data:image/svg+xml;charset=utf-8,%3Csvg width='100' height='100' viewBox='0 0 100 100' fill='none' xmlns='http://www.w3.org/2000/svg'%3E %3Cstyle%3Epath%7Banimation:stroke 5s infinite linear%3B%7D%40keyframes stroke%7Bto%7Bstroke-dashoffset:776%3B%7D%7D%3C/style%3E%3ClinearGradient id='g' x1='0%25' y1='0%25' x2='0%25' y2='100%25'%3E%3Cstop offset='0%25' stop-color='%232d3561' /%3E%3Cstop offset='25%25' stop-color='%23c05c7e' /%3E%3Cstop offset='50%25' stop-color='%23f3826f' /%3E%3Cstop offset='100%25' stop-color='%23ffb961' /%3E%3C/linearGradient%3E %3Cpath d='M1.5 1.5 l97 0l0 97l-97 0 l0 -97' stroke-linecap='square' stroke='url(%23g)' stroke-width='3' stroke-dasharray='388'/%3E %3C/svg%3E") 1;
}

.padding-75{
  padding: .75rem;
}

.text-between {
  vertical-align: -webkit-baseline-middle;
}

.h2 {
  color: white;
  font-size: 18px;
  font-weight: bold;
}

.h3 {
  color: white;
  font-size: 14px;
  font-weight: bold;
}

.h4 {
  color: white;
  font-size: 12px;
  font-weight: bold;
}

.h5 {
  color: white;
  font-size: 11px;
  font-weight: bold;
}

.h6 {
  color: white;
  font-size: 8px;
  font-weight: bold;
}

.weather-body>* {
  display: block;
  width: auto;
}

.weather-description>* {
  display: block;
  width: auto;
  padding-left: 25px;
}

.temperature>* {
  display: block;
  width: auto;
  padding-left: 25px;
}

.atmospheric>* {
  display: block;
  width: auto;
  padding-left: 25px;
}

.wind>* {
  display: block;
  width: auto;
  padding-left: 25px;
}

.rain > *{
  display: block;
  width: auto;
  padding-left: 25px;
}

.snow > *{
  display: block;
  width: auto;
  padding-left: 25px;
}



.weather-detail {
  padding: 0.5rem;
}

.weather-body {
  padding-left: 50px;
  padding-right: 10px;
}

.close-left-hide {
  display: none;
}

.close-left-show {
  display: block;
  float: right;
  padding: .2rem;
  position: absolute;
  height: 15px;
  width: 15px;
  top: 50px;
  right: 53px;
  z-index: 9999999;
}

.leftright {
  top: 2px;
  height: 4px;
  width: 15px;
  position: absolute;
  margin-top: 10px;
  background-color: var(--softorange);
  border-radius: 2px;
  transform: rotate(45deg);
  transition: all .3s ease-in;
}

.rightleft {
  top: 2px;
  height: 4px;
  width: 15px;
  position: absolute;
  margin-top: 10px;
  background-color: var(--softorange);
  border-radius: 2px;
  transform: rotate(-45deg);
  transition: all .3s ease-in;
}

.close {
  margin: 60px 0 0 5px;
  position: absolute;
}

.close-container:hover .leftright {
  transform: rotate(-45deg);
  background-color: var(--tomatored);
}

.close-container:hover .rightleft {
  transform: rotate(45deg);
  background-color: var(--tomatored);
}

.close-container:hover label {
  opacity: 1;
}

.left-weather {
  cursor: pointer;
  border: dashed 2px white;
  margin: 0.5rem 0;
  padding: 0.25rem;
  position: relative;
}

.chart{
  margin: auto;
  top: 75px;
  left: 50px;
  min-width: 40vw;
  max-width: 60vw;
  max-height: 80vh;
  min-height: 50vh;
  float: left;
  z-index: 999999;
  overflow: hidden;
  position: absolute;
  background: white;
  border-radius: 4px;
}

.forecast-button{
  position: absolute;
  right: 10px;
  top: 5px;
  color: white;
  font-weight: 400;
  font-size: 14px;
  border-radius: 3px;
  background: linear-gradient(green,yellow);
}

.forecast-button:hover{
  transform: scale(1.2);
  transition-duration: 500ms;
  background: linear-gradient(red,yellow);
}

.forecast-button{
  transform: scale(1);
  transition-duration: 500ms;
  background: linear-gradient(green,yellow);
}